@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);
/* src/style/index.css */

html,
body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  background: #fafafa;
  font-family: "Press Start 2P", cursive;
  font-size: 12px;
}

input {
  font-family: "Press Start 2P", cursive;
  font-size: 12px;
}

* {
  box-sizing: border-box;
}

button {
  background-color: #e7a61a;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	color: #000;
	border: 0;
	border-radius: 10px;
	box-shadow: 0px 3px 6px 0px #000;
	outline: none;
	font-family: "Press Start 2P", cursive;
  margin-bottom: 1rem;
  cursor: pointer;
}

.main-content {
  padding-top: 3.5rem; /* Hauteur du header */
}

.center-page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100vh;
}

.centerLabel {
  margin: 3rem;
}

.inputContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 2rem; 
}


.header__OVZyn {
	position: fixed;
	left: 0;
	top: 0;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;

	width: 100%;
	height: 3.5rem;

	background: #f59506;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
	z-index: 50;
}

.header__OVZyn a {
	display: inline-block;
	padding: 0 1rem;
	color: #fff;
	text-decoration: none;
	line-height: 3.5rem;
}

.header__OVZyn a:hover,
.header__OVZyn a:active {
	background: rgba(0, 0, 0, 0.2);
}

.header__OVZyn a.logo__jPF5- {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	padding: 0.5rem 1rem;
}

.logo__jPF5- h1 {
	padding: 0 0.5rem;
	font-size: 1.5rem;
	line-height: 2rem;
	font-weight: 400;
}

@media (max-width: 639px) {
	.logo__jPF5- h1 {
		display: none;
	}
}

.header__OVZyn nav a.active__r\+Z6z {
	background: rgba(0, 0, 0, 0.4);
}

